<template>
	<div class="page-navbar OrderBody" ref="122">
	    <mt-navbar class="" v-model="selected">
	      <mt-tab-item id="all" :class="[selectedclass]" :style="[selectedstyle]">全部</mt-tab-item>
	      <mt-tab-item id="undone" :class="[selectedclass]" :style="[selectedstyle]">未完成</mt-tab-item>
	      <mt-tab-item id="done" :class="[selectedclass]" :style="[selectedstyle]">已完成</mt-tab-item>
	    </mt-navbar>
		<div class="content">
		    <mt-tab-container v-model="selected">
		      <mt-tab-container-item class="containeritem" id="all">
		        <OrderBodyItem v-for="item in orders.items" :orderlist="item" :class="[order_part]" @click.native='handleClick(item.code)'></OrderBodyItem>
		      </mt-tab-container-item>
		      <mt-tab-container-item id="undone">
		        <OrderBodyItem v-for="item in undoneOrders.items" :orderlist="item" :class="[order_part]" @click.native='handleClick(item.code)'></OrderBodyItem>
		      </mt-tab-container-item>
		      <mt-tab-container-item id="done">
		        <OrderBodyItem v-for="item in doneOrders.items" :orderlist="item" :class="[order_part]" @click.native='handleClick(item.code)'></OrderBodyItem>
		      </mt-tab-container-item>
		    </mt-tab-container>		
		</div>
	 </div>
</template>

<script>

import OrderBodyItem from './OrderBodyItem.vue';
import { mapGetters, mapActions} from 'vuex';

export default {
	name:'OrderBody',
	data() {
	    return {
	      selected: 'all',
	      selectedclass: 'selectedclass',
	      order_part: 'order_part',
	      selectedstyle: {
	      	fontSize: '0.4rem',
	      }
	    };
	 },
	components: {
		OrderBodyItem
	},
	computed: {
		...mapGetters({
			orders: 'getOrders',
			undoneOrders: 'getundoneOrders',
			doneOrders: 'getdoneOrders'
		})
	},
	methods: {
		...mapActions({
			getOrders: 'getOrders'
		}),
		handleClick: function(code){
			//console.log(code);
			this.$router.push({ path: '/orderDetail' , query:{ code: code }})
		}

	},
	created () {
		this.getOrders('all').catch(error => {
      if(error.body.code == 3001) {
        this.$router.push('/login');
      }
    });
		this.getOrders('done').catch(error => {
      if(error.body.code == 3001) {
        this.$router.push('/login');
      }
    });
		this.getOrders('undone').catch(error => {
      if(error.body.code == 3001) {
        this.$router.push('/login');
      }
    });
		//console.log(this.orders);
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.page-navbar{
		font-size: 0.4rem;
		height: 100%;
	}
	.page-part{
		width: 100%;
		font-size: 0.4rem;
	}
	.selectedclass.is-selected{
		color: #565256;
		border-bottom: 0.06rem solid #d06091;
		font-size: 0.4rem;
	}
	.OrderBody .content{
		width: 100%;
		height: 100%;
		overflow: scroll;
		background-color: #efeeef;
	}
	.order_part{
		width: 100%;
		margin-top: 2%;
		background-color: #fafafa;
	}
	.OrderBody .mint-tab-container-item {
		overflow: scroll !important;
	}
	.OrderBody .mint-tab-container {
		overflow: scroll;
	}
</style>



