<template>
	<div class="orderbodyitem">
		<div class="orderlisthead">
			<p>{{orderlist.code}}</p>
			<p>{{orderlist.serviceType | serviceTypeFilter}}</p>
		</div>
		<div class="orderbodyitemBody">
			<div class="orderbodyitemBodyHead">
				<p style="min-width: 8rem;"><span>订单金额￥{{orderlist.price}}</span></p>
				<p style="color:#d06091">{{orderlist.hgStatus}}</p>
			</div>
			<p><span>护理类型</span>{{orderlist.serviceName}}</p>
			<p><span>下单日期</span>{{orderlist.createdAt}}</p>		
		</div>
	</div>
</template>

<script>
export default {
  	name: 'orderBodyItem',
  	props: ['orderlist']
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.orderbodyitem{
		width: 100%;
		margin-bottom: 0.5rem;
	}
	.orderbodyitem .orderlisthead {
		height: 0.8rem;
		font-size: 0.3rem;
		color: #d06091;
		display: flex;
		justify-content: space-between;
		border-bottom: 2px solid #d06091;
		padding: 0 0.2rem; 
	}
	.orderbodyitem .orderlisthead p {
		height: 0.8rem;
		line-height: 0.8rem;
	}
	.orderbodyitem .orderbodyitemBodyHead {
		font-size: 0.4rem;
		display: flex;
		height: 0.8rem;

		justify-content: space-between;
	}
	.orderbodyitem .orderbodyitemBody {
		font-size: 0.3rem;
		padding: 0.3rem;
	}
	.orderbodyitem .orderbodyitemBody p{
		height: 0.6rem;
		line-height: 0.6rem;
	}
</style>



