<template>
  <div class="indexorder">
    <Order></Order>
  </div>
</template>

<script>
import Order from './Order';
import { mapGetters, mapActions} from 'vuex'
export default {
  name: 'indexOrder',
  components:{
    Order
  },
  methods: {
    ...mapActions({
      changeIndexStatus: 'changeIndexStatus'
    }),
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.changeIndexStatus('order');
    })
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



